.carousel-caption {
    margin-top: 4rem;
    font-family: 'Poppins', 'sans-serif';
    text-align: left;
    color: white;
    z-index: 1000;
}

.carousel-caption h1 {
    margin-top: 1rem;
    font-size: 4.2rem;
    font-weight: 600;
    text-transform: capitalize;
    width: 80%;
    text-shadow: 0 2px 3px rgba(0, 0, 0, .4), 0 4px 7px rgba(0, 0, 0, .1), 0 9px 12px rgba(0, 0, 0, .1);
}

.carousel-caption h4 {
    margin-top: 1rem;
}

.btn-shop button {
    margin-top: 1rem;
    background-color: #f09c05;
    border-radius: 10px 30px;
    color: rgb(255, 255, 255);
    padding: 8px 45px !important;
    font-size: 1.2rem;
}

.btn-shop button:hover {
    border: 2px solid #f09c05;
    background-color: transparent;
    color: #f09c05;
    font-weight: 600;
    font-size: 1.3rem;
}

.carousel-caption {
    position: absolute;
    right: inherit;
    bottom: 0;
    left: 0;
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
    color: #fff;
    text-align: left;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 35px 0 100px;
    background-image: linear-gradient(111deg, #ffffff36 0 0, #fff0 70% 0);
}

@media only screen and (max-width: 1100px) {
    .carousel .carousel-item .img {
        height: 550px;
    }

    .carousel-caption button {
        margin-top: 0.5rem;
        padding: 0.4rem 5rem;
    }

    .carousel-caption h1 {
        font-size: 2.5rem;
    }

}


@media only screen and (max-width: 650px) {
    .carousel-caption button {
        padding: 0.6rem 6rem;
        margin-top: 0.2rem;
    }

}

@media only screen and (max-width: 540px) {
    .carousel .carousel-item .img {
        height: 520px;
    }
}

@media only screen and (max-width: 430px) {
    .carousel-caption button {
        padding: 0.4rem 4rem;
        font-size: 1rem;
    }

    .carousel-caption h1 {
        font-size: 2rem;
    }

    .carousel .carousel-item .img {
        height: 480px;
    }

}

@media only screen and (max-width: 350px) {
    .carousel-caption button {
        padding: 0.6rem 3rem;
    }

    .carousel .carousel-item .img {
        height: 450px;
    }

}

